.page-body {
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  text-align: center;
  font-weight: 400;
  margin: 0 auto;
  max-width: 1200px;
  min-width: 320px;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

.main-header__logo {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
  text-decoration: none;
  display: inline-table;
  width: auto;
  height: 96px;
}

.main-header__block {
  display: block;
  background-image: url("../img/intro-triangle.svg"), url("../img/back-mobile.png");
  background-position: bottom center, bottom center;
  background-repeat: no-repeat;
  background-size: auto, cover;
  min-width: 320px;
  max-width: 1200px;
  background-color: #9bd3f1;
  height: 158px;
  width: auto;
}
