.main-nav {
  position: relative;
  height: 56px;
  width: 100%;
  z-index: 2;
}

.main-nav__wrapper {
  padding: 0;
  margin: 0;
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 21px;
  width: 22px;
  height: 22px;
  border: none;
  background-color: unset;
  z-index: 2;
  cursor: pointer;
}
.main-nav__toggle:active {
  border: none;
}

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.main-nav__list-item {
  width: 100%;
  height: fit-content;
  background: #81b3d2;
  mix-blend-mode: normal;
  margin: 0 auto;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.main-nav__list-item-link {
  display: block;
  width: 100%;
  height: 100%;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 17px 0 11px 1px;
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs {
    margin-bottom: 172px;
  }

  .main-nav--nojs .main-nav__list-item {
    display: block;
  }

  .main-nav--opened .main-nav__list {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }

  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--nojs .main-nav__list {
    display: block;
  }

  .main-nav--opened .main-nav__toggle {
    background-color: rgba(1, 1, 1, 0);
  }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 9px;
    right: -3px;
    width: 28px;
    height: 4px;
    background-color: #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:hover::before, .main-nav--opened .main-nav__toggle:hover::after {
    background-color: rgba(255, 255, 255, 0.6);
  }

  .main-nav--closed .main-nav__toggle {
    background-color: #ffffff;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  }

  .main-nav__list-item {
    border-top: 1px solid rgba(1, 1, 1, 0.2);
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }
  .main-nav__list-item:hover {
    background-color: #669ec0;
  }
  .main-nav__list-item:active {
    background-color: #5496bd;
  }

  .main-nav__list-item-link:active {
    color: rgba(255, 255, 255, 0.3);
  }

  .main-nav--opened .main-nav__list::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }

  .main-nav__list {
    display: flex;
    background-color: #ffffff;
  }

  .main-nav__list-item {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    background-color: #ffffff;
    box-shadow: none;
    border: none;
  }
  .main-nav__list-item:nth-child(2) {
    margin-right: 20%;
  }

  .main-nav__list-item-link {
    color: #000000;
  }
  .main-nav__list-item-link:hover {
    color: #81b3d2;
  }
  .main-nav__list-item-link:active {
    color: #000000;
    mix-blend-mode: normal;
    opacity: 0.3;
  }
  .main-nav__list-item-link:current {
    color: #766357;
  }

  .main-nav__list-item-link--current {
    color: #766357;
  }
}
