.interest__wrapper {
  padding: 37px 0 0;
}

.interest__title {
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  padding: 0 30px;
  margin: 0;
}

.interest__text {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: $description-font-color;
  margin: 0;
  padding: 20px 30px 33px;
  text-transform: uppercase;
}

@media (min-width: $tablet-width) {
  .interest__wrapper {
    padding: 60px 0 10px;
  }

  .interest__title {
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 26px;
  }

  .interest__text {
    padding-top: 32px;
    padding-bottom: 43px;
  }
}

@media (min-width: $desktop-width) {
  .interest__text {
    padding-bottom: 48px;
  }
}
